@import "normalize.css";

@import "../../node_modules/swiper/css/swiper.min.css";

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

html {
  overflow-x: hidden;
}

@font-face {
  font-family: "Rubik";
  font-display: swap;
  font-style: normal;
  font-weight: 300;
  src: local("Rubik-Light"), url("../fonts/Rubik-Light.ttf") format("TrueType");
}

@font-face {
  font-family: "Rubik";
  font-display: swap;
  font-style: normal;
  font-weight: 400;
  src: local("Rubik-Regular"), url("../fonts/Rubik-Regular.ttf") format("TrueType");
}

@font-face {
  font-family: "Rubik";
  font-display: swap;
  font-style: normal;
  font-weight: 500;
  src: local("Rubik-Medium"), local("Rubik Medium"), url("../fonts/Rubik-Medium.ttf") format("TrueType");
}

@font-face {
  font-family: "Rubik";
  font-display: swap;
  font-style: normal;
  font-weight: 700;
  src: local("Rubik-Bold"), url("../fonts/Rubik-Bold.ttf") format("TrueType");
}

.header-logo,
.footer-logo {
  display: flex;
  flex: 1;
  margin-right: 30px;
  order: 0;
}

.header-logo__inner,
.footer-logo__inner {
  display: flex;
}

.footer-logo__text,
.header-logo__text {
  text-transform: uppercase;
  margin-left: 3%;
}

.text-bold {
  font-weight: 700;
}

.nav-menu__list-item {
  display: inline-block;
  margin: 0 5% 0 0;
}

.nav-menu {
  flex: 250px;
}

.nav-menu__list {
  justify-content: center;
  flex: 1;
  display: flex;
}

.signzone {
  display: flex;
  flex: 1;
  justify-content: flex-end;
  order: 1;
}

.signzone__vhod {
  margin-right: 50px;
  padding: 1.3rem 2rem;
  background-color: whitesmoke;
  border-radius: 9px;
}

.signzone__register {
  padding: 1.3rem 1.8rem;
  border-radius: 9px;
  background-color: #f2edff;
}

.screen1 {
  background: url("../img/main.jpg") no-repeat center center;
  background-size: cover;
  min-height: 85vh;
}

.screen1-top__h1 {
  color: #fff;
  text-align: center;
  font-size: 3.75rem;
  padding: 0 11.475rem;
}

.screen1-top__p {
  color: #fff;
  text-align: center;
  font-size: 1.1875rem;
}

.screen1-middle {
  max-width: 1109px;
  width: 100%;
  border-radius: 26px;
  border: 4px solid #f7f8fd;
  background-color: #fff;
  margin: 3.125rem 0;
  opacity: 0.8;
  box-shadow: 0px 5px 20px 3px #cecece;
}

.screen1-top {
  margin-top: 7.0625rem;
}

.screen1-form {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  padding: 4.875rem 2rem 4.6875rem 6.6875rem;
  position: relative;
}

.screen1-form__left,
.screen1-form__middle {
  flex-basis: 350px;
  border-bottom: 1px solid #d6dce2;
  padding-bottom: 0.3125rem;
}

.screen1-form__img {
  padding: 0 1.5625rem 0.9375rem;
  align-self: flex-end;
}

.screen1-form__select {
  border-width: 0;
  padding: 0 1rem 0 3rem;
  border-left: 1px solid #cfd4da;
  -webkit-appearance: none;
  height: 2.1875rem;
  line-height: 1.1875rem;
  color: #4e5983;
  font-weight: 300;
  cursor: pointer;
}

.screen1-form__select_left {
  background: url(../img/sber.png) left 10px center no-repeat, url(../img/arrdown.svg) no-repeat center right;
}

.screen1-form__select_right {
  background: url(../img/cash.png) left 10px center no-repeat, url(../img/arrdown.svg) no-repeat center right;
}

.screen1-form__title {
  font-weight: 500;
  font-size: 1.125rem;
  color: #4e5983;
  padding-bottom: 2rem;
}

.screen1-form__span {
  padding-left: 0.3125rem;
  font-size: 0.75rem;
  line-height: 14px;
  opacity: 0.5;
  color: #4e5983;
}

.screen1-form__subtitle {
  padding-bottom: 1rem;
  font-size: 0.875rem;
  line-height: 17px;
  color: #4e5983;
  opacity: 0.5;
}

.screen1-form__right {
  background: linear-gradient(163.94deg, #562ec2 29.59%, #a07efe 73.59%);
  opacity: 0.8;
  border-radius: 9px;
  align-self: flex-end;
  margin-left: 2rem;
}

.screen1-form__right button {
  font-size: 0.875rem;
  line-height: 17px;
  text-align: center;
  color: #fff;
  cursor: pointer;
  padding: 1.125rem 2.875rem;
}

.screen1-form__input {
  font-size: 1.25rem;
  line-height: 1.5rem;
  letter-spacing: 0.015rem;
  color: #094067;
  border-width: 0;
}

.screen2 {
  padding-top: 5rem;
}

.screen2-col {
  flex: 0 1 300px;
  padding: 0 1.25rem 7.375rem;
}

.screen2-col__img {
  padding-bottom: 1.25rem;
}

.screen2 h3 {
  padding-bottom: 2rem;
  font-weight: 500;
  font-size: 1.375rem;
  line-height: 1.625rem;
}

.screen2-element {
  border: 4px solid #f7f8fd;
  border-radius: 26px;
  display: flex;
  flex-flow: row wrap;
}

.screen2-element .left-side {
  padding: 6.375rem 0 5.5rem 9.6875rem;
  flex: 0 1 400px;
}

.screen2-element .left-side h3 {
  font-weight: 500;
  font-size: 2.1875rem;
  line-height: 2.875rem;
  color: #160647;
}

.screen2-element .left-side p {
  font-size: 18px;
  line-height: 1.875rem;
  color: #4e5983;
  opacity: 0.7;
}

.screen2-element__img {
  padding: 5.9375rem 1.0625rem 7.3125rem 0;
}

.screen2-col__text {
  width: 250px;
}

.screen2-col__text p {
  opacity: 0.7;
  font-size: 1.125rem;
}

.screen3 {
  padding-top: 8.75rem;
}

.screen3-h2 {
  text-align: center;
  font-weight: 500;
  font-size: 2.1875rem;
  line-height: 2.4375rem;
  color: #160647;
}

.screen3-list {
  padding-top: 3.6875rem;
}

.screen3-numbers {
  position: relative;
  flex-basis: 219px;
  margin-right: 2rem;
  padding-bottom: 5.25rem;
}

.screen3-numbers__img {
  position: absolute;
  bottom: 85px;
}

.screen3-numbers__title {
  padding-left: 5.3125rem;
  line-height: 1.1875rem;
  color: #a4aabf;
  font-weight: 400;
  padding-bottom: 2px;
}

.screen3-numbers__text {
  padding-left: 5.3125rem;
  font-size: 1.25rem;
  line-height: 1.9375rem;
  color: #160647;
  height: 2rem;
}

.screen3-comments {
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 3rem;
  position: relative;
}

.screen3-comments__title {
  font-weight: 500;
  font-size: 2.1875rem;
  line-height: 2.875rem;
  color: #160647;
  flex: 100%;
}

.screen3-comments__text {
  margin-top: 7rem;
  padding-bottom: 3.6rem;
  font-size: 1.125rem;
  line-height: 1.875rem;
  color: #4e5983;
  opacity: 0.7;
  min-height: 125px;
}

.screen3-comments__author {
  flex: auto;
  color: #160647;
  font-size: 1.25rem;
  line-height: 1.95rem;
  align-self: flex-end;
}

.screen3-comments__data {
  color: #838ba8;
  font-size: 0.875rem;
  align-self: center;
}

.swiper-container {
  height: 100%;
  position: static;
}

.swiper-slide::before {
  content: "";
  background: url("../img/imgcom.png") no-repeat;
  height: 200px;
  width: 200px;
  position: absolute;
  z-index: -1;
  top: 20%;
  left: -13%;
}

.swiper-button-next,
.swiper-button-prev {
  top: auto;
  bottom: 0;
  border: 2px solid #838ba8;
  opacity: 0.3;
  height: 0;
}

.swiper-button-next {
  width: 4.5rem;
}

.swiper-button-next:after {
  content: "";
  border: solid #838ba8;
  border-width: 4px 4px 0 0;
  position: absolute;
  width: 16px;
  height: 16px;
  right: -3px;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.swiper-button-next:hover {
  border: solid #a17fff;
  border-width: 4px 4px 0 0;
  opacity: 1;
}

.swiper-button-next:hover::after {
  border: solid #a17fff;
  border-width: 4px 4px 0 0;
  opacity: 1;
  bottom: -8px;
  right: -5px;
}

.swiper-button-prev {
  width: 37px;
  right: 100px;
  left: auto;
}

.swiper-button-prev:after {
  content: "";
  border: solid #838ba8;
  border-width: 4px 4px 0 0;
  position: absolute;
  width: 16px;
  height: 16px;
  right: 20px;
  transform: rotate(225deg);
}

.swiper-button-prev:hover::after {
  border: solid #a17fff;
  border-width: 4px 4px 0 0;
  opacity: 1;
}

.swiper-button-prev:hover {
  border: 2px solid #a17fff;
  opacity: 1;
}

.swiper-slide {
  display: flex;
  flex-wrap: wrap;
}

.screen4 {
  padding: 5.625rem 0;
}

.screen4-img {
  position: absolute;
  z-index: 100;
  top: -32px;
  left: 25px;
  transition: 0.3s;
}

.screen4-blur-block {
  position: absolute;
  max-width: 1064px;
  width: 100%;
  height: 341px;
  left: 50px;
  top: 75px;
  background: linear-gradient(163.94deg, #562ec2 29.59%, #a07efe 73.59%);
  filter: blur(150px);
  border-radius: 17px;
}

.screen4-main-block {
  max-width: 1238px;
  height: 447px;
  border-radius: 17px;
  background: linear-gradient(163.94deg, #562ec2 29.59%, #a07efe 73.59%);
  opacity: 0.8;
  position: relative;
}

.screen4-banner {
  max-width: 500px;
  margin: 0 0 0 auto;
  padding: 80px 40px 60px 0;
}

.screen4-banner__title {
  color: #fff;
  font-family: Rubik;
  font-size: 2.1875rem;
  font-weight: 500;
  line-height: 2.9rem;
}

.screen4-banner__text {
  opacity: 0.7;
  color: #fff;
  font-size: 1.125rem;
  font-weight: 400;
  line-height: 1.9rem;
  padding: 0.625rem 0 2.1875rem;
}

.screen4-button {
  background: #fff;
  opacity: 0.9;
  border-radius: 10.2611px;
  padding: 1.5625rem 3.625rem;
  font-weight: 500;
  font-size: 1rem;
  line-height: 1.1875rem;
  text-align: center;
  color: #9f7dfd;
}

.footer-social__icons:not(:last-child) {
  margin-right: 1.625rem;
}

footer {
  padding-bottom: 3.125rem;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: #000000;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  font-family: "Rubik", sans-serif;
  font-size: 16px;
  color: #4e5983;
}

button {
  border-width: 0;
  background: transparent;
  cursor: pointer;
}

header {
  width: 100%;
  padding: 1.25rem 0;
}

.wrapper {
  position: relative;
  width: 100%;
  max-width: 1210px;
  margin: 0 auto;
  padding: 0 15px;
}

.flex {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
}

.align-start {
  align-items: flex-start;
}

.spc-btw-elem {
  justify-content: space-between;
}

.top-button {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  background: #a07efe;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  right: 100px;
  bottom: 100px;
  z-index: 1000;
}

.top-button img {
  position: absolute;
  left: 0;
  top: 0;
}

@media (max-width: 1200px) {
  .wrapper {
    width: 90%;
  }

  .screen1-top__h1 {
    font-size: 3rem;
    padding: 0 7rem;
  }

  .screen1-form {
    padding: 3rem;
    justify-content: center;
  }

  .screen1-form__input {
    width: 55%;
  }

  .screen1-form__select {
    width: 40%;
  }

  .screen1-form__right {
    margin: 2rem auto 1rem;
  }

  .screen2-col {
    flex: 0 1 250px;
  }

  .screen2-element {
    align-items: center;
  }

  .screen2-element .left-side {
    padding: 2rem;
    flex: 300px;
  }

  .screen2-element__img {
    padding: 2rem 0;
  }

  .spc-btw-elem {
    justify-content: space-around;
  }

  .screen3-comments__inner-block {
    padding: 0 3rem 2.5rem 0;
  }

  .screen4-img {
    top: 20px;
    height: 85%;
    left: 20px;
  }

  .screen4-blur-block {
    left: 0;
    bottom: 0;
    width: 90%;
  }

  .screen4-banner__title {
    font-size: 2rem;
  }

  .screen4 .footer-social {
    padding: 1rem 0;
  }
}

@media (max-width: 992px) {
  .screen1-form {
    padding: 2rem 3rem;
  }

  .screen1-form__left,
  .screen1-form__middle {
    flex: 100%;
  }

  .screen1-form__img {
    padding: 1rem;
  }

  .screen4-banner {
    margin: 0 auto;
  }

  .screen4-img {
    left: auto;
    right: 0;
    top: 150px;
    height: 70%;
  }

  .nav-menu__list {
    flex-wrap: wrap;
  }
}

@media (max-width: 768px) {
  .wrapper {
    width: 85%;
  }

  .screen1-top__h1 {
    padding: 0;
    font-size: 2.5rem;
  }

  .nav-menu {
    order: 2;
  }

  .screen3-comments__inner-block {
    flex: 100%;
  }

  .screen3-comments__text {
    margin-top: 2rem;
    min-height: 0;
  }

  .screen4-img {
    right: -40px;
  }

  .nav-menu__list-item {
    padding-top: 0.5rem;
    margin-right: 3%;
  }
}

@media (max-width: 576px) {
  .footer-social {
    padding: 1rem;
    flex: 100%;
  }

  .nav-menu__list-footer {
    flex-flow: column wrap;
    align-items: center;
  }

  .footer-logo,
  .header-logo {
    justify-content: center;
    flex: 100%;
  }

  .signzone {
    justify-content: center;
    padding-top: 10px;
  }

  .screen1-top__h1 {
    font-size: 2.15rem;
  }

  .screen1-form {
    padding: 1rem;
  }

  .screen1-form__title {
    padding-bottom: 1rem;
  }

  .screen1-form__img {
    margin: 0 auto;
    padding: 1rem;
  }

  .screen1-form__right {
    margin: 2rem auto;
  }

  .screen2 {
    padding-top: 2rem;
  }

  .screen2-col {
    justify-content: center;
    display: flex;
    align-items: center;
    padding: 1.5rem;
  }

  .screen2-element {
    margin-top: 4rem;
  }

  .screen2-element .left-side {
    padding: 20px;
  }

  .screen2-element__img {
    padding: 20px 0;
  }

  .spc-btw-elem {
    justify-content: center;
  }

  .screen3-numbers {
    padding-bottom: 2rem;
  }

  .screen3-numbers__img {
    bottom: 2rem;
  }

  .screen3-comments {
    padding: 3rem 0;
  }

  .screen3-comments__inner-block {
    padding: 1rem;
  }

  .screen3-comments__text {
    margin-top: 4rem;
  }

  .screen3-arrows {
    margin-top: 1rem;
    justify-content: center;
  }

  .screen4-img {
    display: none;
  }

  .screen4-blur-block {
    left: 20px;
    width: 90%;
  }

  .screen4-banner {
    padding: 10px;
    display: flex;
    justify-content: center;
    flex-flow: row wrap;
  }

  .screen4-banner__title {
    text-align: center;
    font-size: 1.7rem;
  }

  .screen4-banner__text {
    flex: 100%;
    text-align: center;
  }
}